<template>
  <div class="about">
    <div class="box">
      <div class="title">屏蔽违规资源</div>
      <div class="cnt">
        <!--        <div class="link">-->
        <!--          <div class="t">链接：</div>-->
        <!--          <div class="l" v-text="getLink()"></div>-->
        <!--        </div>-->

        <el-form ref="form" :model="form" label-position="top" label-width="80px">
          <el-form-item label="链接">
            <el-input :value="getLink()" disabled/>
          </el-form-item>
          <el-form-item label="验证码">
            <el-row>
              <el-col :span="15">
                <el-input v-model="form.verifyCode"></el-input>
              </el-col>
              <el-col :span="5">
                <img class="verifyImg" :src="form.verifyImg" @click="getImg" alt="验证码">
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="small" @click="onSubmit">立即屏蔽</el-button>
          </el-form-item>
        </el-form>

        <div class="cmt">
          <p>1.本站的链接均是第三方链接，个人自用，若有违规的链接，可通过此页面自行屏蔽，谢谢。</p>
          <p>2.感谢您的屏蔽，让我们共同维护网络环境</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getLink } from '@/core/helper'
import { reqVerify } from '@/api/verify'
import { reqBanDisk } from '@/api/search'
import { TEXT_INPUT_VERIFY, TEXT_SYS_ERR_PLEASE_TRY_AGAIN } from '@/core/constants'

export default {
  name: 'Ban',
  data () {
    return {
      form: {
        diskType: '',
        diskID: '',
        verifyImg: '',
        verifyStr: '',
        verifyCode: ''
      }
    }
  },
  methods: {
    onSubmit () {
      if (this.form.verifyCode === '') {
        this.$notify.error(TEXT_INPUT_VERIFY)
        return
      }
      reqBanDisk({
        disk_id: this.form.diskID,
        disk_type: this.form.diskType,
        verify_str: this.form.verifyStr,
        verify_code: this.form.verifyCode
      })
        .then((res) => {
          const { code, msg } = res
          if (code === 200) {
            this.$notify.success(msg)
          } else {
            this.$notify.error(msg)
            this.getImg()
          }
        }).catch(() => {
          this.$notify.error(TEXT_SYS_ERR_PLEASE_TRY_AGAIN)
        })
    },
    getLink () {
      return getLink(this.form.diskType, this.form.diskID)
    },
    getImg () {
      reqVerify()
        .then((res) => {
          this.form.verifyImg = res.data.img
          this.form.verifyStr = res.data.id
        })
    }
  },
  mounted () {
    const diskID = this.$route.query.diskID || ''
    const diskType = this.$route.query.diskType || ''

    if (diskID === '' || diskType === '') {
      this.$router.push({ name: 'Home' })
    }
    this.form.diskType = diskType
    this.form.diskID = diskID
    this.getImg()
  }
}
</script>

